.calButton{
    border-radius: 1rem;
    font-size: calc(17px + 0.5vh);
    background-color: rgba($color: #25252567, $alpha: 1.0);
    color: rgba($color: #89eeb6, $alpha: 1.0);
    border: 0px;
    place-items: center;
    
    @media (pointer: fine){
        &:hover{
            background-color: rgba($color: #89eeb6, $alpha: 0.3);
        }
    }

    @media (pointer: coarse){
        &:active{
            background-color: rgba($color: #89eeb6, $alpha: 0.3);
        }
    }

    &.specialButton{
        color: lightcoral;

        @media (pointer: fine){l
            &:hover{        
                background-color: rgba($color: #f08080, $alpha: 0.4);
            }
        }
    
        @media (pointer: coarse){
            &:active{
                background-color: rgba($color: #f08080, $alpha: 0.4);
            }
        }
        
    }
}